/* 
  - CountReducerCount
  - prevStateactionstate
*/
import { INCREMENT, DECREMENT } from "../constant";

// NOTEprevStateprevState=undefinedtype="@@redux/INITxxxxxdata
// prevState
const initState = 0;

export default function countReducer(prevState=initState, action){
  const {type, data} = action;
  // type
  switch(type){
    case INCREMENT:
      // number
      return prevState + data - 0;
      // returnbreak
    case DECREMENT:
      return prevState - data - 0;
    default:
      // 
      return prevState;
  }
}